import './App.css';
import {HashRouter as Router, Switch, Route} from 'react-router-dom';
import Start from './components/Start'
import Info from './components/Info'
import Donar from './components/Donar'
import Dodge from './components/Dodge'
import Analysis from './components/Analysis'
import Play from './components/Play'
import Aproved from './components/Aproved'
import Intermediate from './components/Intermediate'
import Reproved from './components/Reproved'
import Rest from './components/Rest'

function App() {
  return (
    <Router>
      <div className="App">
        <Switch>
          <Route path='/' exact component={Start}/>
          <Route path='/Info' exact component={Info}/>
          <Route path='/Donate' exact component={Donar}/>
          <Route path='/Dodge' exact component={Dodge}/>
          <Route path='/Analysis' exact component={Analysis}/>
          <Route path='/Play' exact component={Play}/>
          <Route path='/Aproved' exact component={Aproved}/>
          <Route path='/Intermediate' exact component={Intermediate}/>
          <Route path='/Reproved' exact component={Reproved}/>
          <Route path='/Rest' exact component={Rest}/>
        </Switch>
      </div>
    </Router>
  );
}

export default App;
