import React, {useState} from 'react';
import Footer from './Footer';
import Header from './Header';
import '../style/Result.css'
import {Link} from 'react-router-dom';

const Aproved = () => {

    const [wined, setWined] = useState(localStorage.getItem('win'));
    const [losed, setLosed] = useState(localStorage.getItem('loss'));
    const [wr, setWr] = useState(localStorage.getItem('winrate')+'%')

    
    return (
        <div>
            <Header text1="Info" text2="Donar" link1='/Info' link2='/Donate'/>
            
            <div className="title">
                <h4>Aprobado</h4>
                <h5>Podes continuar jugado</h5>
            </div>
            <section className="resultados-section">
                <ul className="resultados-nombres">
                    <li>WIN</li>
                    <li>WINRATE</li>
                    <li>LOSS</li>
                </ul>
                <ul className="resultados-stats">
                    <li>{wined}</li>
                    <li>{wr}</li>
                    <li>{losed}</li>
                </ul>
            </section>
            <section className="section-actions">
                <ul>
                    <Link to="Play">
                        <li>Seguir</li>
                    </Link>
                    <Link to="/">
                        <li>Terminar</li>
                    </Link>
                    
                </ul>
            </section>
            <Footer/>
        </div>
    )
}

export default Aproved
