import React, {useState} from 'react';
import Footer from './Footer';
import Header from './Header';
import '../style/Result.css'
import {Link as a, Link} from 'react-router-dom';


const Rest = () => {


    const [wined, setWined] = useState(localStorage.getItem('win'));
    const [losed, setLosed] = useState(localStorage.getItem('loss'));
    const [wr, setWr] = useState(localStorage.getItem('winrate')+'%')

    const goYoutube = ()=>{
        window.open('https://www.youtube.com/')
    }
    const goReddit = ()=>{
        window.open('https://www.reddit.com/')
    }

    return (
        <div>
            <Header text1="Info" text2="Donar" link1='/Info' link2='/Donate'/>
            
            <div className="title">
                <h4>Descanso</h4>
                <h5>Relajate un rato antes de volver a jugar</h5>
            </div>
            <section className="resultados-section">
                <ul className="resultados-nombres">
                    <li>WIN</li>
                    <li>WINRATE</li>
                    <li>LOSS</li>
                </ul>
                <ul className="resultados-stats">
                    <li>{wined}</li>
                    <li>{wr}</li>
                    <li>{losed}</li>
                </ul>
            </section>
            <section className="section-actions">
                <ul>
                    <a>
                        <li onClick={goYoutube}>Youtube</li>
                    </a>
                    <a>
                        <li onClick={goReddit}>Reddit</li>
                    </a>
                    <Link to="/">
                        <li>Terminar</li><a ></a>
                    </Link>
                </ul>
            </section>
            <Footer/>
        </div>
    )
}

export default Rest
