import React from 'react'
import {Link} from 'react-router-dom';
import '../style/Footer.css'

const Footer = () => {

    const Mdev = () => {
        window.open('https://marccosdev.online')
    }

    return (
        <footer id="footer">
            <p>Made by <Link onClick={Mdev}>MDev</Link></p>
        </footer>
    )
}

export default Footer
