import React from 'react'
import {Link} from 'react-router-dom';
import Footer from './Footer';
import Header from './Header';

const Donar = () => {
    return (
        <div>
            <Header text1="Inicio" text2="Info" link1='/' link2='/Info'/>
            <div className="title">
                <h4>Donar</h4>
                <h5>Proximamente</h5>
            </div>
            <Footer/>
        </div>
    )
}

export default Donar
