import React, { useState } from 'react';
import ChampsCard from './ChampsCard';
import Header from './Header';
import RolCard from './RolCard';
import UserCard from './UserCard'
import '../style/Analysis.css'
import Footer from './Footer';
import Loader from './Loader';

const Analysis = () => {

    const [search, setSearch] = useState('');
    const [roles, setRoles] = useState('');
    const [campeones, setCampeones] = useState('');
    const [contenido, setContenido] = useState('');
    const [show, setShow] = useState(false);
    const [server, setServer] = useState('las');
    const [fail, setFail] = useState('')
    const [fetching, setFetching] = useState('')
    const [las, setLas] = useState(true)
    const [lan, setLan] = useState(false)

    const getData = async (e) => {
        e.preventDefault();
        setShow(false)
        setContenido('')
        setFetching(<Loader/>)
        const response = await fetch(`https://jvp2ax6klj.execute-api.us-east-1.amazonaws.com/dev/profile/${search}/${server}`);
        //const response = await fetch(`http://localhost:5000/profile/${search}/${server}`);
        const data = await response.json();
        setFetching('')
        setFail(data)
        setRoles(data[0])
        setCampeones(data[1])
        setSearch('')
        setShow(true)

    }

    const updateSearch = e => {
        setSearch(e.target.value)
    }


    function rols() {
        try {
            setContenido(
                <RolCard
                    name1={roles.rol1.position}
                    played1={roles.rol1.played}
                    winrate1={roles.rol1.winrate}
                    advice1={roles.rol1.advice}
                    name2={roles.rol2.position}
                    played2={roles.rol2.played}
                    winrate2={roles.rol2.winrate}
                    advice2={roles.rol2.advice}
                    name3={roles.rol3.position}
                    played3={roles.rol3.played}
                    winrate3={roles.rol3.winrate}
                    advice3={roles.rol3.advice}
                    name4={roles.rol4.position}
                    played4={roles.rol4.played}
                    winrate4={roles.rol4.winrate}
                    advice4={roles.rol4.advice}
                    name5={roles.rol5.position}
                    played5={roles.rol5.played}
                    winrate5={roles.rol5.winrate}
                    advice5={roles.rol5.advice}
                />)
        } catch (error) {
            console.log('no se establecieron roles')
            
        }
        
    }


    const champs = () => {
        try {
            setContenido(
                <ChampsCard
                    name1={campeones.champ1.name}
                    played1={campeones.champ1.partidas}
                    winrate1={campeones.champ1.winrate}
                    advice1={campeones.champ1.advice}
                    name2={campeones.champ2.name}
                    played2={campeones.champ2.partidas}
                    winrate2={campeones.champ2.winrate}
                    advice2={campeones.champ2.advice}
                    name3={campeones.champ3.name}
                    played3={campeones.champ3.partidas}
                    winrate3={campeones.champ3.winrate}
                    advice3={campeones.champ3.advice}
                    name4={campeones.champ4.name}
                    played4={campeones.champ4.partidas}
                    winrate4={campeones.champ4.winrate}
                    advice4={campeones.champ4.advice}
                    name5={campeones.champ5.name}
                    played5={campeones.champ5.partidas}
                    winrate5={campeones.champ5.winrate}
                    advice5={campeones.champ5.advice}
                    name6={campeones.champ6.name}
                    played6={campeones.champ6.partidas}
                    winrate6={campeones.champ6.winrate}
                    advice6={campeones.champ6.advice}
                    name7={campeones.champ7.name}
                    played7={campeones.champ7.partidas}
                    winrate7={campeones.champ7.winrate}
                    advice7={campeones.champ7.advice}
                    name8={campeones.champ8.name}
                    played8={campeones.champ8.partidas}
                    winrate8={campeones.champ8.winrate}
                    advice8={campeones.champ8.advice}
                    name9={campeones.champ9.name}
                    played9={campeones.champ9.partidas}
                    winrate9={campeones.champ9.winrate}
                    advice9={campeones.champ9.advice}
                    name10={campeones.champ10.name}
                    played10={campeones.champ10.partidas}
                    winrate10={campeones.champ10.winrate}
                    advice10={campeones.champ10.advice}
                />)
        } catch (error) {
            console.log('no se establecieron roles')
        }
        
    }
    let shower = <p></p>
    if (show) {
        try {
            shower =
            <section className='userCard'>
                <UserCard
                    icon={roles.img}
                    name={roles.nombre}
                    img={roles.ligaIMG}
                    league={roles.liga}
                    lp={roles.lp}
                    wins={roles.winsNumber}
                    losses={roles.lossesNumber}
                    total={roles.played}
                    winrate={roles.winrate} />
                <ul className="userCard-ul">
                    <li onClick={rols}>Roles</li>
                    <li onClick={champs}>Campeones</li>
                </ul>

            </section>
        } catch (error) {
            shower = <p className="fail">{fail.notFound}</p>
        }
        
    } else {
        shower = <p></p>
    }
    

    const serverToLas = () => {
        setServer('las')
        setLas(true)
        setLan(false)
    }
    const serverToLan = () => {
        setServer('lan')
        setLas(false)
        setLan(true)
    }

    return (
        <div id="Analysis">
            <Header text1="Info" text2="Donar" link1='/Info' link2='/Donate' />
            <div className="title">
                <h4>An치lisis</h4>
                <h5>Busca tu nombre de invocador y obtene recomendaciones</h5>
            </div>
            <form onSubmit={getData} className='form'>
                <input type="text" value={search} onChange={updateSearch} className='buscador' placeholder={server.toUpperCase()}></input>
                <button type="submit" className="boton btnSearch">Buscar</button>
                <ul className="server-selection">
                    <li className={las ? 'btnServer activo' : "btnServer"} onClick={serverToLas}>LAS</li>
                    <li className={lan ? 'btnServer activo' : "btnServer"} onClick={serverToLan}>LAN</li>
                </ul>
            </form>
            <main >
                <div className="loader">
                    {fetching}
                </div>
                {shower}
                {contenido}
            </main>
            <Footer />
        </div>
    )
}

export default Analysis
