import React from 'react'
import {Link} from 'react-router-dom';
import Footer from './Footer';
import Header from './Header';
import '../style/Start.css'

const Start = () => {
    return (
        <div id='Start'>
            <Header text1="Info" text2="Donar" link1='/Info' link2='/Donate' />
            <div className="title">
                <h1>GitGud</h1>
                <h2>Toma el control de tus partidas</h2>
            </div>
            <main>
                <ul id='start-ul' className="flexUl">
                    <Link to='Dodge'>
                        <li className="btn">
                            Dodge?
                        </li>
                    </Link>
                    <Link to="Play">
                        <li className="btn">
                            Jugar
                        </li>
                    </Link>
                    <Link to="Analysis">
                        <li className="btn">
                            An치lisis
                        </li>
                    </Link>
                </ul>
            </main>
            <Footer/>
        </div>
    )
}

export default Start
