import React from 'react'
import '../style/UsersD.css'

const UsersD = ({name,winrate, liga, lp}) => {
    return (
        <ul className='usersd'>
            <li className='usersd-name'>{name}</li>
            <li>Liga: {liga}</li>
            <li>LP: {lp}</li>
            <li>{winrate}</li>
        </ul>
    )
}

export default UsersD
