import React, {useState} from 'react';
import { Redirect } from 'react-router';
import Aproved from './Aproved';


const Session = ({games}) => {

    const [wined, setWined] = useState(0);
    const [losed, setLosed] = useState(0);
    const [wr, setWr] = useState(0)
    const [i, setI] = useState(0)

    let partidas = games

    const winrate = (val1, val2) =>{
        return (val1/val2)*100
    }

    const wins = ()=>{
        setI(i + 1)
        setWined(wined + 1)
        setWr(~~winrate(wined + 1,i + 1))
        console.log(partidas)
    }

    const losses = ()=>{
        setLosed(losed + 1)
        setI(i +1)
        setWr(~~winrate(wined,i + 1))
        console.log(i)
    }
    const saveData = ()=>{
        localStorage.setItem('win', wined)
        localStorage.setItem('winrate', wr)
        localStorage.setItem('loss', losed)
    }

    if(i == partidas){
        if(wr > 61){
            
            saveData()
            return <Redirect to="/Aproved"></Redirect>
            
        }
        else if(wr > 39){
            saveData()
            return <Redirect to="/Intermediate"/>
        }
        else{
            saveData()
            return <Redirect to="/Reproved"/>
        }
    }  


    return (
        <main className="session">
            <h4>Session</h4>
                <section>
                    <ul className="ul-interactive">
                        <li onClick={wins}>WIN</li>
                        <li onClick={losses}>LOSS</li>
                    </ul>
                    <ul className="ul-names">
                        <li>WIN</li>
                        <li>WINRATE</li>
                        <li>LOSS</li>
                    </ul>
                    <ul className="ul-results">
                        <li>{wined}</li>
                        <li>{wr} %</li>
                        <li>{losed}</li>
                    </ul>
                </section>
            </main>
    )
}

export default Session
