import React, {useState} from 'react';
import {Link} from 'react-router-dom';
import Footer from './Footer';
import Header from './Header';

const Info = () => {

    const [parrafo, setParrafo] = useState(<p>hola papu <br /> sisisi</p>);

    const play = ()=>{
        setParrafo(<p>play papu <br /> sisisi</p>)
    }

    const dodge = ()=>{
        setParrafo(<p>Dodge papu <br /> sisisi</p>)
    }
    const analysis = ()=>{
        setParrafo(<p>Analisis papu <br /> sisisi</p>)
    }

    return (
        <div>
            <Header text1="Inicio" text2="Donar" link1='/' link2='/Donate'/>
            <div className="title">
                <h4>Info</h4>
                <h5>Proximamente</h5>
            </div>
            {/* 
            <main>
                <section>
                    <ul>
                        <li onClick={play}>Jugar</li>
                        <li onClick={analysis}>Consejo</li>
                        <li onClick={dodge}>Dodge?</li>
                    </ul>
                    <p>{parrafo}</p>
                </section>
            </main>*/}
            <Footer/>
        </div>
    )
}

export default Info