import React from 'react'
import '../style/RolCard.css'
const RolCard = ({name1, played1,winrate1,advice1,name2, played2,winrate2,advice2,name3, played3,winrate3,advice3,name4, played4,winrate4,advice4,name5, played5,winrate5,advice5}) => {
    return (
        <div id="RolCard" className="tables">
            <table>
                <tr className="first-tr">
                    <th>Rol</th>
                    <th>Jugadas</th>
                    <th>Winrate</th>
                    <th>Consejo</th>
                </tr>
                <tr>
                    <td>{name1}</td>
                    <td>{played1}</td>
                    <td>{winrate1}</td>
                    <td>{advice1}</td>
                </tr>
                <tr>
                    <td>{name2}</td>
                    <td>{played2}</td>
                    <td>{winrate2}</td>
                    <td>{advice2}</td>
                </tr>
                <tr>
                    <td>{name3}</td>
                    <td>{played3}</td>
                    <td>{winrate3}</td>
                    <td>{advice3}</td>
                </tr>
                <tr>
                    <td>{name4}</td>
                    <td>{played4}</td>
                    <td>{winrate4}</td>
                    <td>{advice4}</td>
                </tr>
                <tr>
                    <td>{name5}</td>
                    <td>{played5}</td>
                    <td>{winrate5}</td>
                    <td>{advice5}</td>
                </tr>
            </table>
            
        </div>
    )
}

export default RolCard
