import React from 'react'
import {Link} from 'react-router-dom';
import '../style/Header.css'

const Header = ({text1,text2,link1,link2}) => {
    return (
        <header>
            <ul id="header-ul">
                <Link to='/' id="icon">
                    <li>GG</li>
                </Link>
                <Link to={link1} id="link1">
                    <li>{text1}</li>
                </Link>
                <Link to={link2} id="link2">
                    <li>{text2}</li>
                </Link>
                <a href="https://discord.gg/DDhNxt9vRB" target="blank" id="link3">Discord</a>
            </ul>
        </header>
    )
}

export default Header
