import React from 'react'

const UserCard = ({name, icon,img, league,lp,wins,losses,total, winrate}) => {
    return (
        <div id='UserCard'>
            <ul className="section-info">
                <li><img src={icon} alt="icon"/></li>
                <li>{name}</li>
            </ul>
            <section className="section-liga">
                <ul className="section-liga-position">
                    <li className="section-liga-li">Liga: {league}<img src={img} alt={league}/></li>
                </ul>
                <ul className="section-liga-stats">
                    <li>LP:</li>
                    <li>{lp}</li>
                </ul>
            </section>
            <section className="section-stats">
                <ul className="section-winsloss">
                    <li>Victorias: {wins}</li>
                    <li>-</li>
                    <li>Derrotas: {losses}</li>
                </ul>
                <ul className="section-totalwinrate">
                    <li>Total: {total}</li>
                    <li>Winrate: {winrate}</li>
                </ul>
            </section>
            
        </div>
    )
}

export default UserCard
