import React, {useState} from 'react';
import Footer from './Footer';
import Header from './Header';
import Session from './Session';
import '../style/Play.css'

const Play = () => {

    const [contenido, setContenido] = useState(
    <main className='choose-sesion'>
        <h4>Elegir Sesion</h4>
        <ul>
            <li onClick={TresGames}>3 Games</li>
            <li onClick={CincoGames}>5 Games</li>
            <li onClick={SieteGames}>7 Games</li>
        </ul>
    </main>);



    function TresGames(){
        console.log('funca')
        setContenido(<Session games={3}/>
        )
    }
    function CincoGames(){
        console.log('funca')
        setContenido(<Session games={5}/>
        )
    }
    function SieteGames(){
        console.log('funca')
        setContenido(<Session games={7}/>
        )
    }

    return (
        <div id='Play'>
            <Header text1="Info" text2="Donar" link1='/Info' link2='/Donate'/>
            {contenido}
            <Footer/>
        </div>
    )
}

export default Play
