import React, {useState} from 'react';
import {Link} from 'react-router-dom';
import '../style/Dodge.css'
import Footer from './Footer';
import Header from './Header';
import Loader from './Loader';
import UsersD from './UsersD';


const Dodge = () => {
    
    const [search, setSearch] = useState('');
    const [us1, setUs1] = useState([]);
    const [us2, setUs2] = useState([]);
    const [us3, setUs3] = useState([]);
    const [us4, setUs4] = useState([]);
    const [us5, setUs5] = useState([]);
    const [advice, setAdvice] = useState([]);
    const [server, setServer] = useState('las');
    const [fail, setFail] = useState('')
    const [show, setShow] = useState(false);
    const [fetching, setFetching] = useState('')
    const [las, setLas] = useState(true)
    const [lan, setLan] = useState(false)

    const getData = async (e) =>  {
        e.preventDefault();
        setShow(false)
        let users = search.split('se uni칩 a la sala')
        console.log('funca esto')
        //const response = await fetch(`http://localhost:5000/liveGame/${server}/${users[0]}/${users[1]}/${users[2]}/${users[3]}/${users[4]}`);
        setFetching(<Loader/>)
        const response = await fetch(`https://jvp2ax6klj.execute-api.us-east-1.amazonaws.com/dev/liveGame/${server}/${users[0]}/${users[1]}/${users[2]}/${users[3]}/${users[4]}`);
        const data = await response.json();
        setFetching('')
        setFail(data)
        console.log(data);
        setUs1(data.user1);
        setUs2(data.user2);
        setUs3(data.user3);
        setUs4(data.user4);
        setUs5(data.user5);
        setAdvice(data.feedback);
        setSearch('');
        setShow(true)
      }

    const updateSearch = e =>{
        setSearch(e.target.value)
    }

    const serverToLas = ()=>{
        setServer('las')
        setLas(true)
        setLan(false)
    }
    const serverToLan = ()=>{
        setServer('lan')
        setLas(false)
        setLan(true)
    }


    let shower = <p></p>
    if (show) {
        try {
            shower =
            <section className='usersd-section'>
                    <UsersD name={us1.name} winrate={us1.winrate} liga={us1.league} lp={us1.pl}/>
                    <UsersD name={us2.name} winrate={us2.winrate} liga={us2.league} lp={us2.pl}/>
                    <UsersD name={us3.name} winrate={us3.winrate} liga={us3.league} lp={us3.pl}/>
                    <UsersD name={us4.name} winrate={us4.winrate} liga={us4.league} lp={us4.pl}/>
                    <UsersD name={us5.name} winrate={us5.winrate} liga={us5.league} lp={us5.pl}/>
            </section>
        } catch (error) {
            shower = <p className="fail">{fail.notFound}</p>
        }
        
    } else {
        shower = <p></p>
    }





    return (
        <div id='Dodge'>
            <Header text1="Info" text2="Donar" link1='/Info' link2='/Donate' />
            <div className="title" >
                <h4>Dodge?</h4>
                <h5>Buscate a vos y a tus compa침eros para recibir recomendaciones</h5>
            </div>
            <form onSubmit={getData} className='form'>
                <input type="text" value={search} onChange={updateSearch} className='buscador' placeholder={server.toUpperCase()}></input>
                <button type="submit" className="boton btnSearch">Buscar</button>
                <ul className="server-selection">
                    <li className={las ? 'btnServer activo' : "btnServer"} onClick={serverToLas}>LAS</li>
                    <li className={lan ? 'btnServer activo' : "btnServer"} onClick={serverToLan}>LAN</li>
                </ul>
            </form>
            
            <main className='dodge-main'>
                <div className="loader">
                    {fetching}
                </div>
                {shower}
                <p className="advice">{advice}</p>                
            </main>
            
            <Footer className="dodge-footer"/>
        </div>
    )
}

export default Dodge
